import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
/**
 * Generated class for the HorarioatencionPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-horarioatencion',
  templateUrl: 'horarioatencion.html',
})
export class HorarioatencionPage {


  	horarioPileta: Array = [];
  	horarioFitness: Array = [];



  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController) {


  	
  }

  ionViewDidLoad() {
    
    let loading = this.loadingController.create({content : "cargando..."});
    loading.present();

    return this.apiProvider.getHorarioAtencion()
    .then(data => {
       this.horarioFitness = data.data[0];
       this.horarioPileta = data.data[1];
   	   loading.dismissAll();
    });


  }

}



